@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lilita+One&family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lilita+One&display=swap);
/* Google fonts to import */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.coming-soon-container {
  text-align: center;
}

.devclub-logo {
  height: 40vmin;
  pointer-events: none;
}

.coming-soon-content {
  background-color: white;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.devchamps-background {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background-image: url(/static/media/devchamps-bg.288b713c.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: grid;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
}

.devchamps-container {
    height: 100%;
    width: 45%;
    min-width: 400px;
    display: grid;
    grid-template-rows: 15vw 1fr auto;
    border-color: #EFE7D8;
    border-width: 1em;
    border-left-style: solid;
    border-right-style: solid;
}

.devchamps-heading {
    background-color: #B34744;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 30% 70%;
}

.ashu {
    margin: 1.5em;
    background-image: url(/static/media/ashu-cool.e688a890.gif);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.title-grid{
    display: grid;
    grid-template-rows: 50% 50%;
}

.title {
    height: 100%;
    width: 100%;
    background-image: url(/static/media/devchamps-heading.6fb618f6.png);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.devchamps-container > .devchamps-heading > .devchamps-ashu {
    min-width: 0;
    min-height: 0;
    height: 100%;
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
}

.faq{
    text-align: center;
    color: #EFE7D8;
    font-family: 'Lilita One', cursive;
    font-size: 5em;
}

.faq-container {
    background-color: #EFE7D8;
    font-family: 'Montserrat';
    display: grid;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
}

.accordions{
    margin: 1em;
}

.devchamps-container > .faq-container .row-title-text {
    margin: 5px;
    margin-left: 20px;
    font-weight: 900;
    color: #5F394B !important;
}

.devchamps-container > .faq-container .row-title-text:active {
    color: #DB8263 !important;
}

.devchamps-container > .faq-container .row-content-text {
    margin-left: 15px;
    margin-right: 15px;
    line-height: 1.5rem;
    text-align: left;
}

.devchamps-container > .faq-container .row-content-text em {
    font-weight: 800;
}

.bottom{
    background-color: #799AAD;
    height: 100%;
    width: 100%;
}

.hof-coming-soon {
    font-family: 'Bebas Neue';
    color: #EFE7D8;
    font-size: 3.5rem;
    text-align: center;
}

@media screen and (max-width: 820px) {
    .devchamps-container {
        grid-template-rows: 25vw 1fr auto;
    }
    
    .hof-coming-soon {
        font-size: 2rem;
    }
}

@media screen and (max-width: 420px) {
    .devchamps-container {
        grid-template-rows: 32vw 1fr auto;
        border-width: 0px;
    }

    .ashu {
        margin: 0.5em;
    }

    .faq{
        font-size: 2.3em;
    }

    .accordions{
        margin: 0.4em 1em 0.4em 1em;
    }

    .hof-coming-soon {
        font-size: 1rem;
    }
}
